.footer {

    &__inner {
        display: flex;
        flex-wrap: wrap;
        max-width: 1000px;
        margin: 0 auto;

        @media (max-width: 1040px) {
            padding: 0 2em; } }

    &__adress {
        flex: 0 0 33.33%;

        @media (max-width: 740px) {
            flex: 0 0 100%; } }

    &__tax {
        opacity: .6;
        padding: .6em .6em 0 0; }

    &__aboutmagellan {
        flex: 0 0 66.66%;

        @media (max-width: 1040px) {
            padding-left: 2em; }

        @media (max-width: 740px) {
            flex: 0 0 100%;
            padding-left: 0;
            padding-top: 2em; } }

    &__hr {
        flex: 0 0 100%;
        margin: 1.5em 0; }

    &__sm {
        @include darkmodefilter(); }

    &__branding {
        display: flex;
        flex: 0 0 50% !important;

        & a {
            font-family: 'Playfair Display','Playfair Display SC';
            color: $grey;
            padding-right: 1.25em; } }

    &__theme {
        flex: 0 0 20%;

        @media (max-width: 924px) {
            flex: 0 0 100% !important; } }

    &__more {
        flex: 0 0 30%;

        &:last-child {
            text-align: right; }

        & img, & svg {
            width: 21px;
            height: 21px;
            margin: 0 .75em; }

        &--logo {
            margin-top: .5em;
            margin-bottom: .3em;
            width: 100px !important; // 100px is the min-width of the UNICEF Brand (https://www.unicef.org/ethiopia/BrandToolKit_EN_September2012.pdf)
            height: 30px; } }

    &__branding, &__theme, &__more {

        @media (max-width: 924px) {
            flex: 0 0 100% !important;
            text-align: center !important;
            justify-content: center;
            margin-bottom: 2em; } } }

footer {

    & p, & a {
        color: #535353;
        margin: 0;
        opacity: 1; } }
